import React from 'react';
import Typography from '@material-ui/core/Typography';
import diagram1 from './diagram1.png';
import diagram2 from './diagram2.png';

const styles = {
  background: {
    backgroundColor: '#E4E0EE',
    padding: 30,
  },
  panel: {
    maxWidth: 1000,
    margin: 'auto',
  },
  contentSection: {
    marginTop: 50,
    marginBottom: 50,
  },
  contentSubsection: {
    marginTop: 15,
  },
  diagram: {
    maxWidth: '100%',
  },
};

const About = () => {
  return (
    <div style={styles.background}>
      <div style={styles.panel}>
        <div style={styles.contentSection}>
          <Typography variant="display1" gutterBottom>Motivations</Typography>
          <Typography variant="body1" gutterBottom>
            The idea for the Electronic Tonicizer came about when I was playing saxophone
            in a funk band with a few of my friends. I greatly enjoy playing funk, jazz,
            and other types of music on my saxophone, but since my saxophone isn't my main
            instrument, I am not proficient enough to be able to freely play it at the level
            that I wish in all twelve keys. I might be able to crank out a decent solo in
            G major, but switch to G# minor and suddenly I'm stumbling through the scale.
          </Typography>
          <Typography variant="body1" gutterBottom>
            This made me think - what if there was a wind instrument that I could play
            that would allow me to play in different keys at the press of a button?
            Instead of having fingerings for every specific equally tempered pitch like
            a traditional saxophone, clarinet, bassoon, etc, you could label the pitches
            using relative Solfege and simply change the pitch of "Do" every time you
            wanted to change pitch. This is the main idea behind the Electronic Tonicizer.
          </Typography>
          <div style={styles.contentSubsection}>
            <Typography variant="headline" gutterBottom>Users and Use Cases</Typography>
            <Typography variant="body1" gutterBottom>
              The expected users of the Electronic Tonicizers are wind instrument players
              who want to be able to play tonal music (especially melodic lines) in many
              different keys without a steep learning curve. The instrument takes very little
              time to pick up and play for a musician who is already trained in a different
              instrument, even if that instrument is not a wind instrument. It is a portable
              instrument, meaning you can take it to jam sessions and just play without
              having to set up a laptop or a mixer. One can imagine using the Electronic
              Tonicizer to solo over a funk groove or a jazz standard with many changes.
            </Typography>
          </div>
        </div>

        <div style={styles.contentSection}>
          <Typography variant="display1" gutterBottom>The Instrument</Typography>
          <Typography variant="body1" gutterBottom>
            As with most wind instruments, the Electronic Tonicizer is a monophonic
            instrument. The sound is produced through a software synthesizer running on
            a <a href="https://www.raspberrypi.org/">Raspberry Pi</a>, which is
            essentially a small computer. This means you can simply change the type of
            sample on the software synthesizer to change the sound of the instrument.
            The instrument has an octave thumbstick, mimicking the octave keys found on
            many of the commonly used wind instruments. The instrument also has what is
            essentially a whammy bar, allowing the player to change the timbre of the
            sound as they play (something NOT commonly found on many wind instruments).
          </Typography>

          <div style={styles.contentSection}>
            <img src={diagram1} style={styles.diagram} />
            <img src={diagram2} style={styles.diagram} />
          </div>

          <div style={styles.contentSubsection}>
            <Typography variant="headline" gutterBottom>Hardware</Typography>
            <Typography variant="body1" gutterBottom>
              The instrument is essentially a series of senors hooked up through a Raspberry Pi
              in order to turn the user input into sound. The player's breath, which is fed
              through a tenor saxophone mouthpiece, is detected by a force sensitive resistor.
              There are two 200mm softpots that serve as the pitch input (left side) and the
              key change input (right side). The thumbstick acts as an octave key, allowing
              the pitches specified by the left softpot to be transposed up or down an octave,
              and the flex sensor on the right side of the instrument acts as the timbral input.
              The instrument can be powered by a wall charger as well as a typical mobile phone
              battery pack for portability.
            </Typography>
            <Typography variant="body1" gutterBottom>
              All of these sensors are wired using mini breadboards inside the body of the instrument,
              which then are connecte to the GPIO pins on the Raspberry Pi. From there, the Python
              code running inside of the Raspberry Pi converts those input signals into MIDI signals.
            </Typography>
          </div>
          <div style={styles.contentSubsection}>
            <Typography variant="headline" gutterBottom>Software</Typography>
            <Typography variant="body1" gutterBottom>
              A custom written Python program running on the Raspberry Pi using
              the <a href="https://gpiozero.readthedocs.io/en/stable/">gpiozero</a> package accepts the
              inputs from the GPIO ports of the Raspberry Pi and converts them to corresponding
              MIDI signals, which are then sent out to the MIDI channel of the amsynth software
              synthesizer. Since the Raspberry Pi runs Linux, <a href="http://jackaudio.org/">JACK Audio</a> is
              used to reduce latency. Amsynth then produces the actual sound that comes out of either
              the 3.5mm jack of the Raspberry Pi or through a Bluetooth speaker.
            </Typography>
          </div>
        </div>

        <div style={styles.contentSection}>
          <Typography variant="display1" gutterBottom>Further Study</Typography>
          <Typography variant="body1" gutterBottom>
            This current form of the Electronic Tonicizer is a great proof of concept, but in
            order for the instrument to be truly viable as a robust consumer product, much work
            needs to be done. There are a few major quality of life problems to be fixed, such as
            the leackage of air from the mouthpiece and the ergonomics of the right hand interacting
            with the "whammy bar". There are also calibration issues that seem somewhat unavoidable
            with the current hardware/software choices, as the breath magnitude detection and the
            pitch detection give slightly different values each time the instrument is used.
            The general packaging would also need an improvement, as the cardboard to mouthpiece
            connection is currently quite flimsy.
          </Typography>
        </div>
      </div>
    </div>
  );
};

export default About;


