import React from 'react';
import Typography from '@material-ui/core/Typography';

const styles = {
  background: {
    backgroundColor: '#E4E0EE',
    padding: 30,
  },
  panel: {
    maxWidth: 750,
    margin: 'auto',
    textAlign: 'center',
  },
};

const SubtitleCard = () => {
  return (
    <div style={styles.background}>
      <div style={styles.panel}>
        <Typography variant="subheading" gutterBottom>
          The Electronic Tonicizer is an electronic wind instrument designed to make
          playing tonal music in multiple keys easier. The instrument is easy to learn
          for wind instrument players and non-wind players alike. It was designed and
          created by Joon Park as his final project for the Digital Luthier course
          (EECS 397-0-5) taught by Professor Bryan Pardo at Northwestern University.
        </Typography>
      </div>
    </div>
  );
};

export default SubtitleCard;


