import React from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import cover from './cover.jpg';
import './TitleCard.css';

const styles = {
  background: {
    height: 1000,
    position: 'fixed',
    left: 0,
    right: 0,
    top: -180,
    display: 'block',
    zIndex: -1,
    backgroundImage: `url(${cover})`,
    backgroundSize: '100%',
    filter: 'blur(5px)',
  },
  titleText: {
    marginTop: 50,
    marginBottom: 50,
    textAlign: 'center',
  },
  mainTitle: {
    marginBottom: 50,
    color: 'white',
  },
  subtext: {
    color: 'white',
  },
  titleContainer: {
    zIndex: 1,
    padding: 30,
  },
};

const TitleCard = () => {
  return (
    <div>
      <div style={styles.background}>
      </div>

      <div style={styles.titleContainer}>
        <div style={styles.titleText} id="title-text">
          <Typography variant="display3" gutterBottom style={styles.mainTitle}>The Electronic Tonicizer</Typography>

          <Grid container spacing={24}>
            <Grid item xs={12} sm={6}>
              <Typography variant="subheading" gutterBottom style={styles.subtext}>A final project for</Typography>
              <Typography variant="headline" gutterBottom style={styles.subtext}>Digital Luthier, EECS 397-0-5</Typography>
              <Typography variant="subheading" gutterBottom style={styles.subtext}>Northwestern University</Typography>
              <Typography variant="subheading" gutterBottom style={styles.subtext}><a href="http://www.cs.northwestern.edu/~pardo/">Professor Bryan Pardo</a></Typography>
            </Grid>

            <Grid item xs={12} sm={6}>
              <Typography variant="subheading" gutterBottom style={styles.subtext}>Designed and created by</Typography>
              <Typography variant="headline" gutterBottom style={styles.subtext}><a href="http://joonpark.me/">Joon Park</a></Typography>
              <Typography variant="subheading" gutterBottom style={styles.subtext}>JoonPark@u.northwestern.edu</Typography>
            </Grid>
          </Grid>
        </div>
      </div>
    </div>
  );
};

export default TitleCard;


